import React, { useEffect, useState } from "react";
import { Container, Typography, Grid, Grow, AppBar } from '@material-ui/core';

import user from './images/user.png';
import Posts from './components/Posts/Posts';
import Form from './components/Form/Form';
import useStyle from './style';
import { useDispatch } from 'react-redux';
import { getPosts } from './actions/posts';

// we will track the current id from this component because
// this is the only component were post and form component are there
// and we need current id for both post and form to update data
// without redux we need to bring all the thing in app component

const App = () => {
  const [currentId, setCurrentId] = useState(null);
  const classes = useStyle();

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getPosts());
  }, [currentId, dispatch])
  return (
    <>
      <Container maxidth="lg">
        <AppBar className={classes.appBar} position="static" color="inherit">
          <Typography className={classes.heading} variant="h4" align="center">Memories</Typography>
          <img className={classes.image} src={user} alt="memories" height="60" />
        </AppBar>
        <Grow in>
          <Container>
            <Grid className={classes.mainContainer} container justify="space-between" alignItems="stretch" spacing={3}>
              <Grid item xs={12} sm={7}>
                {/*here setCurrentId is used for setter method */}
                <Posts setCurrentId={setCurrentId} />
              </Grid>
              <Grid item xs={12} sm={4}>
                <Form currentId={currentId} setCurrentId={setCurrentId} />
              </Grid>
            </Grid>
          </Container>
        </Grow>
      </Container>
    </>
  )
}

export default App;